import React from "react";
import { Link } from "gatsby";
import "./Courses.scss";

const Courses = ({ edges, isHorizontal }) =>
  (isHorizontal ? (
    <div className="CourseList--isHorizontal">
      {edges.map(edge => (
        <div className="CourseList-item" key={edge.node.fields.slug}>
          <h2 className="CourseList-item-title">
            <Link className="CourseList-item-title-link" to={edge.node.fields.slug}>
              {edge.node.frontmatter.title}
            </Link>
          </h2>
          <div className="CourseList-item-meta">
            <span className="CourseList-item-meta-category">
              <Link
                to={edge.node.fields.categorySlug}
                className="CourseList-item-meta-category-link"
              >
                {edge.node.frontmatter.category}
              </Link>
            </span>
          </div>
          <Link className="CourseList-item-readmore" to={edge.node.fields.slug}>
            Go Learn
          </Link>
        </div>
      ))}
    </div>
  ) : (
    <div className="CourseList">
    {edges.map(edge => (
      <div className="CourseList-item" key={edge.node.fields.slug}>
        <div className="CourseList-item-meta">
          <span className="CourseList-item-meta-category">
            <Link
              to={edge.node.fields.categorySlug}
              className="CourseList-item-meta-category-link"
            >
              {edge.node.frontmatter.category}
            </Link>
          </span>
        </div>
        <h2 className="CourseList-item-title">
          <Link className="CourseList-item-title-link" to={edge.node.fields.slug}>
            {edge.node.frontmatter.title}
          </Link>
        </h2>
        <p className="CourseList-item-description">
          {edge.node.frontmatter.description}
        </p>
        <Link className="CourseList-item-readmore" to={edge.node.fields.slug}>
          Go Learn
        </Link>
      </div>
    ))}
  </div>
  ));

export default Courses;
